      //       el.style.backgroundColor = this.contents.tableBtnDetailBgColor
      //     })
      //     document.querySelectorAll('.table-content .tables .el-table__body .el-button--primary').forEach(el=>{
      //       el.style.height = this.contents.tableBtnHeight
      //       el.style.color = this.contents.tableBtnEditFontColor
      //       el.style.fontSize = this.contents.tableBtnFontSize
      //       el.style.borderWidth = this.contents.tableBtnBorderWidth
      //       el.style.borderStyle = this.contents.tableBtnBorderStyle
      //       el.style.borderColor = this.contents.tableBtnBorderColor
      //       el.style.borderRadius = this.contents.tableBtnBorderRadius
      //       el.style.backgroundColor = this.contents.tableBtnEditBgColor
      //     })
      //     document.querySelectorAll('.table-content .tables .el-table__body .el-button--danger').forEach(el=>{
      //       el.style.height = this.contents.tableBtnHeight
      //       el.style.color = this.contents.tableBtnDelFontColor
      //       el.style.fontSize = this.contents.tableBtnFontSize
      //       el.style.borderWidth = this.contents.tableBtnBorderWidth
      //       el.style.borderStyle = this.contents.tableBtnBorderStyle
      //       el.style.borderColor = this.contents.tableBtnBorderColor
      //       el.style.borderRadius = this.contents.tableBtnBorderRadius
      //       el.style.backgroundColor = this.contents.tableBtnDelBgColor
      //     })

      //   }, 50)
      // })
    },
    // 
    contentPageStyleChange() {
      let arr = []
      if (this.contents.pageTotal) arr.push('total')
      if (this.contents.pageSizes) arr.push('sizes')
      if (this.contents.pagePrevNext) {
        arr.push('prev')
        if (this.contents.pagePager) arr.push('pager')
        arr.push('next')
      }
      if (this.contents.pageJumper) arr.push('jumper')
      this.layouts = arr.join()
      this.contents.pageEachNum = 10
    },

    init() {
    },
    search() {
      this.pageIndex = 1;
      this.getDataList();
    },
    // 
    getDataList() {
      this.dataListLoading = true;
      let params = {
        page: this.pageIndex,
        limit: this.pageSize,
        sort: 'id',
      }


      if (this.searchForm.caipinName != '' && this.searchForm.caipinName != undefined) {
        params['caipinName'] = '%' + this.searchForm.caipinName + '%'
      }

      if (this.searchForm.caipinTypes != '' && this.searchForm.caipinTypes != undefined) {
        params['caipinTypes'] = this.searchForm.caipinTypes
      }

      if (this.searchForm.yonghuName != '' && this.searchForm.yonghuName != undefined) {
        params['yonghuName'] = '%' + this.searchForm.yonghuName + '%'
      }

      if (this.searchForm.caipinOrderUuidNumber != '' && this.searchForm.caipinOrderUuidNumber != undefined) {
        params['caipinOrderUuidNumber'] = '%' + this.searchForm.caipinOrderUuidNumber + '%'
      }

      if (this.searchForm.caipinOrderTypes != '' && this.searchForm.caipinOrderTypes != undefined) {
        params['caipinOrderTypes'] = this.searchForm.caipinOrderTypes
      }

      params['caipinOrderDelete'] = 1//  1  2 


      this.$http({
        url: "caipinOrder/page",
        method: "get",
        params: params
      }).then(({data}) => {
        if (data && data.code === 0) {
          this.dataList = data.data.list;
          this.totalPage = data.data.total;
        } else {
          this.dataList = [];
          this.totalPage = 0;
        }
        this.dataListLoading = false;
      });

      //
      this.$http({
        url: "dictionary/page?dicCode=caipin_types&page=1&limit=100",
        method: "get",
        page: 1,
        limit: 100,
      }).then(({data}) => {
        if (data && data.code === 0) {
          this.caipinTypesSelectSearch = data.data.list;
        }
      });
      //
      //
      this.$http({
        url: "dictionary/page?dicCode=caipin_order_types&page=1&limit=100",
        method: "get",
      }).then(({data}) => {
        if (data && data.code === 0) {
          this.caipinOrderTypesSelectSearch = data.data.list;
        }
      });
    },
    //
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 
    selectionChangeHandler(val) {
      this.dataListSelections = val;
    },
    // /
    addOrUpdateHandler(id, type) {
      this.showFlag = false;
      this.addOrUpdateFlag = true;
      this.crossAddOrUpdateFlag = false;
      if (type != 'info') {
        type = 'else';
      }
      this.$nextTick(() => {
        this.$refs.addOrUpdate.init(id, type);
      });
    },
    // 
    download(file) {
      window.open(" ${file} ")
    },
    // 
    dayinOpen(item) {
      let _this = this;
      _this.printVisiable = true;
      _this.printFrom = item;
    },
    // 
    printContent() {
      let _this = this;
      _this.$print(_this.$refs.printContent)
      _this.printVisiable = false;
    },
    // 
    deleteHandler(id) {
      var ids = id ? [Number(id)] : this.dataListSelections.map(item => {
        return Number(item.id);
      });

      this.$confirm(`[${id ? "" : ""}]?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "caipinOrder/delete",
          method: "post",
          data: ids
        }).then(({data}) => {
          if (data && data.code === 0) {
            this.$message({
              message: "",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.search();
              }
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    },
    // 
    caipinOrderUploadSuccess(data) {
      let _this = this;
      _this.$http({
        url: "caipinOrder/batchInsert?fileName=" + data.file,
        method: "get"
      }).then(({data}) => {
        if (data && data.code === 0) {
          _this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
              _this.search();
            }
          });
        } else {
          _this.$message.error(data.msg);
        }
      });

    },
    // 
    caipinOrderUploadError(data) {
      this.$message.error('');
    },

    //
    refund(id) {
      this.$confirm(`?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "caipinOrder/refund?id=" + id,
          method: "post",
        }).then(({data}) => {
          if (data && data.code === 0) {
            this.$message({
              message: "",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.search();
              }
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    },
    //
    deliver(id) {
      let _this = this;
      _this.$confirm(`?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        _this.$http({
          url: "caipinOrder/deliver?id=" + id,
          method: "post",
        }).then(({data}) => {
          if (data && data.code === 0) {
            _this.$message({
              message: "",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.search();
              }
            });
          } else {
            _this.$message.error(data.msg);
          }
        });
      });
    },
    //
    receiving(id) {
      this.$confirm(`?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "caipinOrder/receiving?id=" + id,
          method: "post",
        }).then(({data}) => {
          if (data && data.code === 0) {
            this.$message({
              message: "",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.search();
              }
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    },
    //
    openCommentModel(id) {
      //id
      this.commentbackId = null
      //
      this.commentbackContent = null
      //
      this.commentbackId = id
      //
      this.commentbackVisible = true
    },
    //
    commentback(id) {
      var _this = this
      if (this.commentbackContent == null || this.commentbackContent == 0) {
        this.$message.error("");
        return false;
      }
      _this.$confirm(`?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        _this.$http({
          url: "caipinOrder/commentback?id=" + _this.commentbackId + "&commentbackText=" + _this.commentbackContent + "&caipinCommentbackPingfenNumber=" + _this.caipinCommentbackPingfenNumber,
          method: "post",
        }).then(({data}) => {
          if (data && data.code === 0) {
            _this.$message({
              message: "",
              type: "success",
              duration: 1500,
              onClose: () => {
                _this.search();
                //id
                _this.commentbackId = null
                //
                _this.commentbackContent = null
                //
                _this.commentbackVisible = false
                //
                _this.caipinCommentbackPingfenNumber = null
              }
            });
          } else {
            //id
            _this.commentbackId = null,
                //
                _this.commentbackContent = null,
                //
                _this.commentbackVisible = false,
                //
                _this.caipinCommentbackPingfenNumber = null
            _this.$message.error(data.msg);
          }
        });
      });
    },
    //
    wuyong(id) {
      let _this = this;
      _this.$confirm(`    ?`, "", {
        confirmButtonText: "", cancelButtonText: "", type: "warning"
      }).then(() => {
        _this.$http({
          url: "caipinOrder/update",
          method: "post",
          data: {
            id: id,
//                            caipinOrderTypes:caipinOrderTypes,
          }
        }).then(({data}) => {
          if (data && data.code === 0) {
            _this.$message({
              message: "", type: "success", duration: 1500, onClose: () => {
                _this.search();
              }
            });
          } else {
            _this.$message.error(data.msg);
          }
        });
      });
    },
  }
};
</script>
<style lang="scss" scoped>
.slt {
  margin: 0 !important;
  display: flex;
}

.ad {
  margin: 0 !important;
  display: flex;
}

.pages {
  & /deep/ el-pagination__sizes {
    & /deep/ el-input__inner {
      height: 22px;
      line-height: 22px;
    }
  }
}


.el-button + .el-button {
  margin: 0;
}

.tables {
  & /deep/ .el-button--success {
    height: 40px;
    color: #333;
    font-size: 14px;
    border-width: 2px;
    border-style: dashed;
    border-color: rgba(255, 255, 255, 1);
    border-radius: 20px;
    background-color: var(--publicSubColor);
  }

  & /deep/ .el-button--primary {
    height: 40px;
    color: #333;
    font-size: 14px;
    border-width: 2px;
    border-style: dashed;
    border-color: rgba(255, 255, 255, 1);
    border-radius: 20px;
    background-color: var(--publicSubColor);
  }

  & /deep/ .el-button--danger {
    height: 40px;
    color: #333;
    font-size: 14px;
    border-width: 2px;
    border-style: dashed;
    border-color: rgba(255, 255, 255, 1);
    border-radius: 20px;
    background-color: var(--publicSubColor);
  }

  & /deep/ .el-button {
    margin: 4px;
  }
}

.form-content {
  background: transparent;
}

.table-content {
  background: transparent;
}

.tables /deep/ .el-table__body tr {
  background-color: rgba(255, 255, 255, 1) !important;
  color: rgba(92, 93, 95, 1) !important;
}

.tables /deep/ .el-table__body tr.el-table__row--striped td {
  background: transparent;
}

.tables /deep/ .el-table__body tr.el-table__row--striped {
  background-color: #F5F7FA !important;
  color: #606266 !important;
}

.tables /deep/ .el-table__body tr:hover > td {
  background-color: #f5f5f5 !important;
  color: #333 !important;
}</style>


